<template>
  <div class="classic-detail-page">
    <div class="page-bg">
      <div class="page-bg-gradient" />
    </div>
    <div class="detail-inner">
      <div v-if="loading" class="loading-wrap">
        <el-icon class="is-loading" :size="32"><Loading /></el-icon>
      </div>
      <template v-else-if="item">
        <div class="main-card">
          <p class="original content-font">{{ item.original_text }}</p>
          <div v-if="item.translation" class="translation-wrap">
            <span class="translation-label">翻译</span>
            <p class="translation">{{ item.translation }}</p>
          </div>
          <dl class="meta">
            <template v-if="item.source">
              <dt>出处</dt>
              <dd>{{ item.source }}</dd>
            </template>
            <template v-if="item.author || item.dynasty">
              <dt>作者</dt>
              <dd>
                <template v-if="item.dynasty">［{{ item.dynasty }}］</template>
                {{ item.author }}
              </dd>
            </template>
            <template v-if="item.genre">
              <dt>体裁</dt>
              <dd>{{ item.genre }}</dd>
            </template>
            <template v-if="itemCategories(item).length">
              <dt>分类</dt>
              <dd class="meta-categories">
                <span
                  v-for="cat in itemCategories(item)"
                  :key="cat"
                  :class="['category-tag', getCategoryTagClass(cat)]"
                >
                  {{ cat }}
                </span>
              </dd>
            </template>
          </dl>
          <div class="actions">
            <el-button :loading="likeLoading" @click="toggleLike">
              <el-icon v-if="likedThisSession"><StarFilled /></el-icon>
              <el-icon v-else><Star /></el-icon>
              {{ item.likes }} 点赞
            </el-button>
            <el-button
              v-if="authStore.isAuthenticated"
              :loading="favLoading"
              @click="toggleFavorite"
            >
              <span :class="['action-icon-heart', { 'is-filled': isFavorited }]" />
              {{ isFavorited ? '已收藏' : '收藏' }}
            </el-button>
            <el-button v-else @click="goLogin">登录后收藏</el-button>
          </div>
        </div>
        <section v-if="similarList.length" class="similar-section">
          <h2 class="section-title">相似推荐</h2>
          <div class="similar-grid grid-wrap">
            <router-link
              v-for="s in similarList"
              :key="s.id"
              :to="`/classic/item/${s.id}`"
              class="item-card"
            >
              <div class="card-inner">
                <h3 class="card-original">{{ s.original_text }}</h3>
                <div class="card-footer">
                  <span class="card-meta">
                    <template v-if="s.dynasty">［{{ s.dynasty }}］</template>
                    <template v-if="s.author">{{ s.author }}</template>
                    <template v-if="s.author && s.source"> · </template>
                    <template v-if="s.source">{{ s.source }}</template>
                    <span v-if="s.genre" class="card-genre">{{ s.genre }}</span>
                  </span>
                  <div class="card-right">
                    <span class="card-likes">
                      <el-icon :size="14"><Star /></el-icon>
                      {{ s.likes ?? 0 }}
                    </span>
                    <template v-for="cat in itemCategories(s)" :key="cat">
                      <span :class="['category-tag', getCategoryTagClass(cat)]">{{ cat }}</span>
                    </template>
                  </div>
                </div>
              </div>
            </router-link>
          </div>
        </section>
      </template>
      <div v-else class="empty">条目不存在</div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, onMounted } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { Loading, Star, StarFilled } from '@element-plus/icons-vue'
import { useAuthStore } from '@/stores/auth'
import { getItem, similar, updateLike, favorite, unfavorite } from '@/api/classic'
import type { ClassicItem } from '@/types/classic'
import { ElMessage } from 'element-plus'

defineOptions({ name: 'ClassicItemDetail' })

const route = useRoute()
const router = useRouter()
const authStore = useAuthStore()

const item = ref<ClassicItem | null>(null)
const similarList = ref<ClassicItem[]>([])
const loading = ref(true)
const likeLoading = ref(false)
const favLoading = ref(false)
const isFavorited = ref(false)
const likedThisSession = ref(false)

const id = computed(() => {
  const raw = route.params.id
  const n = parseInt(String(raw), 10)
  return Number.isNaN(n) ? 0 : n
})

const categoryTagMap: Record<string, string> = {
  格物修身: 'cat-purple',
  济世情怀: 'cat-indigo',
  道德情操: 'cat-sky',
  人与自然: 'cat-cyan',
  哲理思辨: 'cat-emerald',
  文化传承: 'cat-lime',
  理想信念: 'cat-amber',
  成长成才: 'cat-orange',
  家国情怀: 'cat-red',
}
function itemCategories(item: ClassicItem): string[] {
  const raw = item.categories
  if (!raw) return []
  const arr = Array.isArray(raw)
    ? raw
    : String(raw)
        .split(',')
        .map((s: string) => s.trim())
  return arr.slice(0, 2)
}
function getCategoryTagClass(category: string): string {
  return categoryTagMap[category.trim()] ?? 'cat-gray'
}

async function load() {
  if (!id.value) return
  loading.value = true
  try {
    const [detail, sim] = await Promise.all([getItem(id.value), similar(id.value)])
    item.value = detail
    similarList.value = sim ?? []
  } catch {
    item.value = null
    similarList.value = []
  } finally {
    loading.value = false
  }
}

async function toggleLike() {
  if (!item.value || likeLoading.value) return
  const action = likedThisSession.value ? 'decrement' : 'increment'
  likeLoading.value = true
  try {
    const res = await updateLike(item.value.id, { action })
    if (item.value) item.value.likes = res.likes
    likedThisSession.value = !likedThisSession.value
  } finally {
    likeLoading.value = false
  }
}

async function toggleFavorite() {
  if (!item.value || favLoading.value || !authStore.isAuthenticated) return
  favLoading.value = true
  try {
    if (isFavorited.value) {
      await unfavorite(item.value.id)
      isFavorited.value = false
      ElMessage.success('已取消收藏')
    } else {
      await favorite(item.value.id)
      isFavorited.value = true
      ElMessage.success('已收藏')
    }
  } catch (e) {
    ElMessage.error((e as Error).message)
  } finally {
    favLoading.value = false
  }
}

function goLogin() {
  router.push({ path: '/login', query: { redirect: route.fullPath } })
}

// 只在首次挂载时加载，因为每个详情页都有独立的缓存实例（通过 route.fullPath 作为 key）
onMounted(load)
</script>

